import { css } from 'lit';

export const style = css`
  :host {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100px;
    max-height: 6vh;
    background-color: #6200ee;
    text-align: center;
    color: #000000;
  }
  h1 {
    font-size: 2em;
    padding: 5px;
    margin: auto;
  }
  #header-accent {
    height: .5em;
    background-color: #000051;
    width: 100%;
  }
`;